import React, { FC, useCallback, useEffect, useState } from 'react';
import { useRecoilCallback, useRecoilState, useRecoilValue } from 'recoil';
import { getAllMovies } from '@recoil/selectors/movieSelector';
import { v4 as uuidv4 } from 'uuid';
import { Movie } from 'types';
import MovieContents from './MovieContents';
import ErrorBoundary from '@components/ErrorBoundary';

interface props {
  movies?: Movie[];
}

const Movies: FC<props> = ({ movies }) => {
  const loadableMovies = useRecoilValue(getAllMovies);
  console.log(loadableMovies);
  return (
    <ErrorBoundary>
      <React.Suspense fallback={<div>Loading</div>}>
        {loadableMovies.map(
          (loadableMovie, index) =>
            ({
              hasValue: (
                <MovieContents
                  cursor={index + 1}
                  key={uuidv4()}
                  movies={loadableMovie.contents}
                />
              ),
              hasError: <div key={uuidv4()}>Error</div>,
              loading: <div key={uuidv4()}>Loading</div>,
            }[loadableMovie.state]),
        )}
      </React.Suspense>
    </ErrorBoundary>
  );
};

export default Movies;
